button,
[type="button"],
[type="reset"],
[type="submit"] {
  appearance: none; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  clip: rect(0 0 0 0); }

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  overflow-x: hidden; }
  .wrapper--no-scrollbar {
    height: 100vh;
    -ms-overflow-style: none;
    scrollbar-width: none; }
    .wrapper--no-scrollbar::-webkit-scrollbar {
      display: none; }
  .wrapper .header,
  .wrapper .footer {
    flex-shrink: 0; }
  .wrapper main {
    flex-grow: 1; }

.scroll-lock {
  position: fixed;
  overflow-y: auto; }

.btn-reset {
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer; }

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 20px; }
  @media (max-width: 767px) {
    .container {
      padding: 0 10px; } }
  @media (max-width: 767px) {
    .container--no-padding {
      padding: 0; } }

.header {
  padding-top: 20px; }

.header__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: flex-start; }

.header__logo {
  padding-top: 3px;
  line-height: 0; }

.header__logo img {
  width: 45px;
  max-width: 45px;
  height: 37px; }

@media screen and (max-width: 768px) {
  .header__logo img {
    position: relative;
