  border-radius: 40px; }

@media screen and (max-width: 768px) {
  .header__wrapper p {
    min-width: 175px;
    margin-right: 80px; } }

@media screen and (max-width: 955px) {
  .header .btn {
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 23px;
    padding-right: 23px;
    font-size: 14px;
    line-height: 17px; } }

@media screen and (max-width: 768px) {
  .header .btn {
    position: relative;
    z-index: 20; } }

.main-nav {
  width: 53.8%;
  padding-top: 10px; }

@media screen and (max-width: 1055px) {
  .main-nav {
    width: 60%; } }

@media screen and (max-width: 955px) {
  .main-nav {
    padding-top: 8px; } }

@media screen and (max-width: 790px) {
  .main-nav {
    width: 65%; } }

